@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{font-size:62.5%;box-sizing:border-box}body{font:1.6rem 'Poppins', sans-serif;height:100vh;margin:0}a{text-decoration:none;color:black}.App{box-sizing:border-box;height:100vh}.App .grid-container{display:grid;grid-template-areas:'header' 'main' 'footer';grid-template-columns:1fr;grid-template-rows:5rem 1fr 5rem;height:100%}.App .grid-container .header{grid-area:header;display:flex;justify-content:space-between;align-items:center;padding:1rem}.App .grid-container .header button{font-size:3rem;padding:0.5rem;background:none;border:none;color:black;cursor:pointer}.App .grid-container .header .brand a{color:black;font-size:3.5rem;font-weight:bold;text-decoration:none}.App .grid-container .main{grid-area:main}.App .grid-container .footer{grid-area:footer;display:flex;justify-content:center;align-items:center}.App .grid-container .sidebar{position:fixed;transition:all 0.5s;transform:translateX(-30rem);width:30rem;background-color:white;height:100%}.App .grid-container .sidebar a{text-decoration:none;color:#000000}.App .grid-container .sidebar a:hover{color:#bebebe}.App .grid-container .sidebar.open{transform:translateX(0)}.App .grid-container .sidebar-close-button{border-radius:50%;border:0.1rem #000000 solid;background-color:white;width:3rem;height:3rem;padding:0.5rem;font-size:2rem;padding-top:0;cursor:pointer;position:absolute;right:0.5rem;top:1.5rem}.products{display:flex;justify-content:center;align-items:center;flex-wrap:wrap}.products li{list-style-type:none;padding:0;flex:0 1 34rem;margin:2rem;height:50rem;border-bottom:0.1rem #c0c0c0 solid}.products li .product{display:flex;flex-direction:column;justify-content:space-between;height:100%}.products li .product .product-name{font-size:2rem;font-weight:bold}.products li .product .product-name a:hover{text-decoration:underline;color:cadetblue}.products li .product .product-price{font-size:2rem;font-weight:bold;display:flex;justify-content:space-between}.products li .product .product-image{max-width:34rem;max-height:34rem}.details{display:flex;justify-content:space-between;align-items:flex-start;flex-wrap:wrap;padding:1rem}.details .details-image{flex:2 1 60rem}.details .details-image img{max-width:60rem;width:100%}.details .details-info{flex:1 1 30rem}.details .details-action{flex:1 1 30rem}.details .details-info ul,.details .details-action ul{list-style-type:none;padding:0}.details .details-info li,.details .details-action li{margin-bottom:1rem}.details .back-to-result{padding:1rem}.details .details-action{border:0.1rem solid;border-radius:0.5rem;padding:1rem}.details .details-action ul li:last-child{display:flex;flex-direction:column}.button{padding:1rem;border:0.1rem solid;border-radius:0.5rem;cursor:pointer}.button:hover{border:0.1rem #404040 solid}.button.primary{background-color:#40c7f0}.button.secondary{background-color:#f0f0f0}.text-center{text-align:center}

